import { createRouter, createWebHashHistory } from "vue-router";

const routes = [
  {
    path: '/',
    name: 'Index',
    component: () => import('@/views/index'),
    meta: {
      title: '',
      // keepAlive: true
    }
  },
  {
    path: '/user',
    name: 'User',
    component: () => import('@/views/user'),
    meta: {
      title: '我的资产',
      // keepAlive: true
    }
  },
  {
    path: '/share',
    name: 'Share',
    component:() => import('@/views/share'),
    meta: {
      title: ''
    }
  },
  {
    path: '/ido',
    name: 'Ido',
    component: () => import('@/views/ido'),
    meta: {
      title: 'Ido',
      // keepAlive: true
    }
  },
  {
    path: '/stake',
    name: 'StakeList',
    component: () => import('@/views/stake'),
    meta: {
      title: 'Stake',
      // keepAlive: true
    }
  },
  {
    path: '/buyNft',
    name: 'BuyNft',
    component: () => import('@/views/buyNft'),
    meta: {
      title: 'BuyNft',
      // keepAlive: true
    }
  },
  {
    path: '/market',
    name: 'Market',
    component: () => import('@/views/market'),
    meta: {
      title: 'Market',
      // keepAlive: true
    }
  },
  {
    path: '/market/my',
    name: 'MarketMy',
    component: () => import('@/views/market/my'),
    meta: {
      title: 'MarketMy',
      // keepAlive: true
    }
  },
  
    {
    path: '/lpStake',
    name: 'LpStake',
    component: () => import('@/views/lpStake'),
    meta: {
      title: 'LpStake',
      // keepAlive: true
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

export default router;
